import React, {useEffect, useState} from "react";
import Header from "../../components/Header/Header";
import Navigation from "../../components/Navigation/Navigation";
import Slider from "react-slick";
import * as S from "./styles";

function Info(props) {
  const [showSlides, setShowSlides] = useState(true);
  const { header, headerColor, textBlack, textGray, text, slides, slidesShowTime, buttons, buttonsColor } = props.data;
  const { setNextPage,  setPrevPage, isVisible } = props;

  useEffect(() => {
    if(isVisible){
      setTimeout(() => {
        setShowSlides(false);
      }, slidesShowTime);
    }
  }, [isVisible, slidesShowTime]);

  return (
      <S.Container isVisible={isVisible}>
        <Header title={header} color={headerColor} isVisible={isVisible}/>

        <S.TextContainer className={"setResizeText"}>
        {showSlides ? <Slider {...{
            fade: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false,
            autoplay: true,
            autoplaySpeed: slidesShowTime / slides.length,
          }}>
            {slides.map((item, index) => <S.Slides key={index + item} className={"setResizeText"}>{item}</S.Slides>)}
          </Slider> :
          <>
            <S.Text>{text}</S.Text>
            <S.TextGray>{textGray}</S.TextGray>
            <S.TextBlack>{textBlack}</S.TextBlack>
            <Navigation buttonsColor={buttonsColor} textAnimationButton={buttons[1]} setNextPage={setNextPage} setPrevPage={setPrevPage} />
          </>
        }
        </S.TextContainer>
      </S.Container>
  );
}

export default Info;
